      console.log('submit', values)
      let id = new Date().getTime()
      this.myArray.push({
        name: 9,
        id: id,
        url: 'www.baidu.com',
        title: 9,
      })
    },
    bodyClick() {
      Object.keys(this.modelWrapIsShow).forEach((item) => {
        console.log(item)
        this.modelWrapIsShow[item] = false
      })
      this.$forceUpdate()
    },
    addSite() {
      this.addTitle = '新增站点'
      this.show = true
    },
    addGroup() {
      // this.show = true
      let id = new Date().getTime()
      this.myArray.push({
        name: 66,
        id: id,
        sonItem: [],
        title: 66,
      })
    },
    itemWrapClick(id) {
      console.log('点击')
      console.log(id)
      if (this.modelWrapIsShow[id]) {
        this.modelWrapIsShow[id] = !this.modelWrapIsShow[id]
      } else {
        this.modelWrapIsShow[id] = true
      }
      this.$forceUpdate()
      console.log(this.modelWrapIsShow)
    },
    itemClick(id) {},
  },
}
